export default {
	props: {
		tabBars: Array,
		tabIndex: Number,
		scrollStyle: {
			type: String,
			default: ''
		},
		scrollItemStyle: {
			type: String,
			default: ''
		}
	},
	data () {
		return {}
	},
	methods: {
		tabTap (index) {
			this.$emit('fixIndex',index)
		}
	}
}
